import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from 'src/app/service/api.service';
import { API_URL } from 'src/app/common/api';
import { HttpParams } from '@angular/common/http';
import { Topics, Topic } from 'src/app/model/Topic.model';
import { ToastNotificationService } from 'src/x/http/toast-notification.service';

@Component({
  selector: 'app-topic',
  templateUrl: './topic.component.html',
  styleUrls: ['./topic.component.scss']
})
export class TopicComponent implements OnInit {
  paramId = this.route.snapshot.params['id']
  @Input() category_id: number 
  topicArray: Topics[]
  
  constructor(
    private route        : ActivatedRoute,
    private http         : ApiService,
    private toastService : ToastNotificationService
  ) { }

  ngOnInit() {
  }

  ngOnChanges(changes: { [x: string]: { currentValue: number; }; }) {
    if (changes['category_id']) {
      this.category_id = changes['category_id'].currentValue;
      this.getAllTopic(this.category_id);
    }
  }

  /**
   * function get all topics 
   * @param category_id 
   */
  getAllTopic(category_id){
    let params = new HttpParams();
    params = params.append('category_id', category_id);
    params = params.append('user_id', '7');
    this.http.getAllTopic(params).subscribe(
      (res:Topic) => {
        switch (res['code']){
          case 200:
              this.topicArray = res.data;
              break;
          case 201:
              this.toastService.success(res['message']);
              break;
          default:
        }
      }
    )
  }

}
