import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SharedService } from 'src/app/common/shared/shared.service';
import { ApiService } from 'src/app/service/api.service';
import { UpdateUserInfo, UserInfo } from 'src/app/model/userInfo.model';
import { ToastNotificationService } from 'src/x/http/toast-notification.service';
declare var $: any;
@Component({
  selector: 'app-edit-user-info',
  templateUrl: './edit-user-info.component.html',
  styleUrls: ['./edit-user-info.component.scss']
})
export class EditUserInfoComponent implements OnInit {
  registerForm: FormGroup;
  submitted = false;
  user_Firstname: string;
  user_Lastname: string;
  user_Avatar: string;
  user_Email: string;
  numberPhone= '0964696431';

  constructor(
    private formBuilder : FormBuilder,
    private shared      : SharedService,
    private apiService  : ApiService,
    private toast       : ToastNotificationService
  ) { }

  ngOnInit() {
    this.validatorsForm()
    this.getDataUser()
  }

  /**
   * valid test function
   */
  validatorsForm(){
    this.registerForm = this.formBuilder.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      phone: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
    });
  }

  // convenience getter for easy access to form fields
  get f() { return this.registerForm.controls; }

  /**
   * function to update user information 
   */
  onSubmit(valueForm:FormGroup, fileThumb:HTMLInputElement){
    this.submitted = true;
    // stop here if form is invalid
    if (this.registerForm.invalid) {
        return;
    }
    const name = valueForm.value.firstName.trim() + ' ' +valueForm.value.lastName.trim()
    var formData = new FormData()
    if(fileThumb.files[0]){
      formData.set('image', fileThumb.files[0])
    }
    formData.set('name', name)
    this.apiService.updateUserInfo(formData).subscribe(
      (res:UpdateUserInfo) => {
        this.toast.success(res['message'])
      }
    )
  }

  /**
   * function select image file
   * @param event 
   */
  onFileChanged(event: any){
    this.shared.previewImage(event,'avatar')
  }


  /**
   * function get user information
   */
  getDataUser(){
    this.apiService.getUserInfo().subscribe(
      (res:UserInfo ) => {
        if(res.code == 200){
          const arrName = res.data.name.split(' ');
          this.user_Firstname= arrName[0];
          
          var arrLastName =  []
          arrName.forEach((item,key) => {
            if(key != 0){
              arrLastName.push(item)
            }
          });
          this.user_Lastname = arrLastName.join(' ');
          this.user_Avatar = res.data.avatar;
          this.user_Email  = res.data.email;
          this.toast.success(res['message']);
        }
      }
    )
  }
}
