import { Injectable } from '@angular/core';
import { HttpService } from 'src/x/http/http.service';
import { API_URL } from '../common/api';
import { UserInfo, UpdateUserInfo } from '../model/userInfo.model';
import { Categories } from '../model/Categories.model';
import { Topic } from '../model/Topic.model';
import { Word } from '../model/words.model'
import { from } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(
    private http: HttpService
  ) { }

  getUserInfo(){
    return this.http.Get<UserInfo>(API_URL.getUser)
  }

  updateUserInfo(body:any){
    return this.http.Post<UpdateUserInfo>(API_URL.updateUser, body)
  }

  getAllCategory(){
    return this.http.Get<Categories>(API_URL.getCategories)
  }

  getAllTopic(params: any){
    return this.http.Get<Topic>(API_URL.getTopic, params)
  }
  onSendResult(body){
    return this.http.Post(API_URL.sendResult,body)
  }

  getAllWord(params: any){
    return this.http.Get<Word>(API_URL.getWord, params)
  }
}