import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/service/api.service';
import { ToastNotificationService } from 'src/x/http/toast-notification.service';
import { UserInfo } from 'src/app/model/userInfo.model';
import { Categories, dataCategory } from 'src/app/model/Categories.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  categoryArray:dataCategory[]
  category_id: number
  body ={ 'topic_id':7 , 'stars':1000}
  constructor(
    private apiService   : ApiService,
    private toastService : ToastNotificationService,
  ) { }

  ngOnInit() {
    this.getAllCategory();
  }


  /**
   * function get all categories
   */
  getAllCategory(){
    this.apiService.getAllCategory().subscribe(
      (res: Categories) => {
        this.categoryArray = res.data;
        this.category_id = this.categoryArray[0].id;
        this.toastService.success(res['message']);
      }
    )
  }

  /**
   * function Selected categories
   * @param id 
   */
  onSelectedCategory(id:number){
    this.category_id = id;
  }
}
