import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/service/api.service';
import { HttpParams } from '@angular/common/http';
import { Word, Words } from 'src/app/model/words.model';
import { ActivatedRoute } from '@angular/router';
import { ToastNotificationService } from 'src/x/http/toast-notification.service';

@Component({
  selector: 'app-list-word',
  templateUrl: './list-word.component.html',
  styleUrls: ['./list-word.component.scss']
})
export class ListWordComponent implements OnInit {
  paramId: string;
  dataWord: Words[] 
  constructor(
    private apiService : ApiService,
    private route      : ActivatedRoute,
    private toast       : ToastNotificationService
  ) { }

  ngOnInit() {
    this.getAllWord();
  }

  /**
   * function Get params in url
   */
  getParamsUrl(){
    this.route.params.subscribe(
      params=> {
        this.paramId = params.topic_id
      }
    )
  }

  /**
   * function Get all the words in the topic
   */
  getAllWord(){
    this.getParamsUrl()
    let params = new HttpParams();
    params = params.append('topic_id', this.paramId);
    this.apiService.getAllWord(params).subscribe(
      (res: Word) => {
        switch(res['code']){
          case 200:
            this.dataWord = res.data;
            break;
          case 201:
            this.toast.success(res['message'])    
            break;   
            default:
        }
      })
  }

}
